body {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: white;
  color: #333;
  font-family: Arial, sans-serif;
  overflow: hidden;
}

.app {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  text-align: center;
}

.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #6200ee;
  color: white;
  padding: 10px 20px;
}

.profile {
  display: flex;
  align-items: center;
}

.profile span {
  margin-left: 10px;
  font-size: 1.2em;
}

.settings {
  display: flex;
  align-items: center;
}

.app-main {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.user-info {
  margin-top: 20px;
}

.user-info p {
  margin: 5px 0;
  font-size: 1.2em;
}

.app-footer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #6200ee;
  color: white;
  padding: 10px 0;
}

.footer-button {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: none;
  border: none;
  color: white;
  font-size: 1em;
  cursor: pointer;
}

.footer-button span {
  margin-top: 5px;
}
